



























































































































































































import { Component, Vue, Prop } from 'vue-property-decorator';
import Module, { Color, RequestFn, FormatFn, HoverIndex } from './PluginModule.vue';


@Component({

})
export default class MultiComboBox<M> extends Vue {
  readonly optionTmpl = 'option';

  private models: M[] = [];
  private selected: M[] = [];
  private filter = '';
  private filterTimerID!: NodeJS.Timer;
  private hoverIndex: HoverIndex = null;
  private isSelecting = false; 

  @Prop() value!: any;
  @Prop() color?: Color;

  @Prop() private options!: RequestFn<M> | Array<M>;
  @Prop() private valueField!: string;
  @Prop() private placeholder?: string;
  @Prop() private displayFormat?: FormatFn<M>;


  get isRemote(): boolean {
    return this.options instanceof Function;
  }

  get isFilterFocused(): boolean {
    return $(this.$refs.filter).is(':focus');
  }

  get colorCode(): string {
    return 'string' === typeof this.color && this.color.trim().length > 0? this.color : Color.PRIMARY;
  }

  get doDisplayFormat(): FormatFn<M> {
    return this.displayFormat instanceof Function ? this.displayFormat
      : ((dataModel: M): string => (dataModel as any)[this.valueField]);
  }

  get datalist(): M[] {
    const filters = this.filter.split(' ').filter(param => param.trim().length > 0);

    return this.isRemote ? this.models : (this.options as M[]).filter((model: any) =>
      filters.length === 0 || filters.filter((param: string) =>
        Object.keys(model).filter((fieldName: string) =>
          model[fieldName].toString().toUpperCase().indexOf(param.toUpperCase()) >= 0
        ).length > 0
      ).length > 0
    );
  }

  created() {
    if (this.value !== undefined) {
      if (!this.isRemote) {
        const mappings = this.datalist.filter((dataModel: M) => this.value.indexOf((dataModel as any)[this.valueField]) >= 0);

        if (mappings.length === 0) 
          this.value = [];
        else
          this.selected = this.selected.concat(mappings);
      } else {

      }
    }
  }

  mounted(): void {
    $(this.$el).on('hide.bs.dropdown', () => {
      return !this.isSelecting || this.hoverIndex !== null;
    });
  }

  doSelect(dataModel: M): void {
    this.selected.push(dataModel);

    this.$emit('input', this.selected.map(selected => (selected as any)[this.valueField]));
    this.$emit('selected', this.selected);

    this.onFilterBlur();

    $(this.$refs.menu).addClass('show');
  }

  doDisselect(dataModel: M): void {
    this.selected.splice(this.selected.indexOf(dataModel), 1);

    this.$emit('input', this.selected.map(selected => (selected as any)[this.valueField]));
    this.$emit('disselected', dataModel);
  }

  onFilterInput(): void {
    this.filter = $(this.$refs.filter).text();

    if (this.isRemote) {
      clearTimeout(this.filterTimerID);

      this.filterTimerID = setTimeout(() =>
        (this.options as RequestFn<M>)(this.filter.split(' ').filter(param => param.trim().length > 0))
          .then((datalist: M[]) => this.models = datalist)
          .then(() => clearTimeout(this.filterTimerID)),
        400
      );
    }
  }

  onFilterBlur(): void {
    this.filter     = '';
    this.hoverIndex = null;

    $(this.$refs.filter).text('');
  }

  onOptionHoverChange(newIndex?: HoverIndex): void {
    if (newIndex === undefined || newIndex === null || 'number' !== typeof newIndex || isNaN(newIndex))
      this.hoverIndex = null;
    else {
      const count = $(this.$refs.menu).find(' > a.ui-option').length;

      this.hoverIndex = (newIndex + count) % count; 
    }
  }
}
