




























































import { Component, Vue } from 'vue-property-decorator';

import Switcher from './@components/Switcher.vue';
import ComboBox from './@components/ComboBox.vue';
import MultiComboBox from './@components/MultiComboBox.vue';
import Textfield from './@components/Textfield.vue';


interface IComboBoxModel {
  value: string;
  text: string;
}

interface IMultiModel {
  code: string;
  fullName: string;
}

@Component({
  components: {
    Switcher,
    ComboBox,
    MultiComboBox,
    Textfield
  }
})
export default class UiPlugin extends Vue {
  private color = 'danger';
  private textValue = '';
  private switcherTimerID!: NodeJS.Timer;
  private switchValue = false;
  private comboValue  = 'primary';
  private multiValues: string[] = ['B231', 'A324'];

  private comboOpts: IComboBoxModel[] = [
    {value : 'primary' , text  : 'Blue'},
    {value : 'info'    , text  : 'Cyan'},
    {value : 'success' , text  : 'Green'},
    {value : 'warning' , text  : 'Yellow'},
    {value : 'danger'  , text  : 'Red'}
  ];

  private multiOpts: IMultiModel[] = [
    {code: 'A001' , fullName: 'Tom'},
    {code: 'B231' , fullName: 'Mary'},
    {code: 'A324' , fullName: 'John'},
    {code: 'B243' , fullName: 'Jane'},
    {code: 'C002' , fullName: 'Tommy'},
    {code: 'J110' , fullName: 'Taco'},
    {code: 'I843' , fullName: 'June'},
    {code: 'K333' , fullName: 'Daniel'},
    {code: 'K233' , fullName: 'Daisy'},
    {code: 'C100' , fullName: 'Abraham'},
    {code: 'G100' , fullName: 'Nicholas'}
  ];

  onTurnOn(): void {
    this.switcherTimerID = setTimeout(() => this.switchValue = false, 6000);
  }

  onTurnOff(): void {
    console.log('Turn Off', this.switchValue);
  }

  onComboChanged(selected: IComboBoxModel, isSelected: boolean): void {
    console.log(isSelected ? 'Selected' : 'Disselected', selected);
  }
}
